
@media (min-device-pixel-ratio: 1.5), (-webkit-device-pixel-ratio: 1.5) {
.border-1px::after {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (min-device-pixel-ratio: 2), (-webkit-device-pixel-ratio: 2) {
.border-1px::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
#tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 40px;
  line-height: 40px;
}
#tab .tab-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  text-align: center;
  position: relative;
}
#tab .tab-item::after {
  display: block;
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  content: ' ';
}
#tab a {
  display: block;
  color: #4d555d;
  font-size: 14px;
}
#tab .router-link-active {
  color: #f01414;
}
